@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'BioDare2 Heatmap';

  size = 25;
  lastX = 120;
  gaps = false;
  normalized = true;
  middleZero = false;

  sizes = [10, 25, 50, 100, 200, 500];
  pageIndex = 0;

  data: Serie[] = JSON.parse(dataJson72);

  msgI = 1;

  busy = false;

  ngOnInit(): void {
    this.size = this.data.length;
    this.lastX = Math.max(...this.data.map(s => s.data[s.data.length - 1].x));

  }

  message() {
    console.log('Message', this.msgI++);
    return 'Message';
  }

  testData() {
    return this.normalized ? JSON.parse(dataNorm120) : JSON.parse(dataRaw120);
  }

  /**
  loadDataPage(page: PageEvent) {
    console.log('Load page', page);
    this.pageIndex = page.pageIndex;
    this.size = page.pageSize;

  }*/

  nextSize(size: number) {
    this.size = size;
    this.generate();
  }

  generate() {

    this.data = this.testData();

    while (this.data.length < this.size) {
      this.data = this.data.concat(this.testData());
    }

    this.data = this.data.slice(0, this.size);

    this.data.forEach((v, i) => {
      v.key = i;
      v.label = `${i}. ${v.label}`;
    });

    this.data.forEach(s => s.data = this.extend(s.data, this.lastX));

    if (this.gaps) {
      this.data.forEach(s => s.data = this.makeGaps(s.data, 10));
    }
  }


  extend(data: Point[], lastX: number) {


    while (data.length <= lastX + 1) {
      const ext: Point[] = data.map(p => ({x: p.x, y: p.y}));
      data = data.concat(ext);
    }

    let x = data[0].x;
    data.forEach(p => p.x = x++);

    const last = data.findIndex(p => p.x > lastX);
    return data.slice(0, last);
  }

  makeGaps(data: Point[], every: number) {
    const limit = 1 / every;

    return data.map(p => Math.random() < limit ? undefined : p)
      .filter(p => !!p);
  }
}
